import React, { Fragment, useState } from "react"
import axios from "axios"
import { trackPromise } from "react-promise-tracker"


const UploadForm = props => {
  const { setDisplayAlert, inspectionID,   currentSU, currentMF, setShowSharePointFiles, sharePointPath, inspectionDate, setUploadResult } = props
  const [selectedFile, setSelectedFile] = useState()
  
  const handleFile = event => {
    setSelectedFile({ file: event.target.files })
  }

  const handleUpload = async () => {
    //event.preventDefault()
    const files = selectedFile.file

    // for (let i = 0; i < files.length; i++) {
    //   formData.append("files[]", files[i])
    // }
    // console.log("formData", formData.getAll("files[]"))

    //const inspection_date = inspectionDate.getMonth() + 1 + "/" + inspectionDate.getDate() + "/" + inspectionDate.getFullYear()

      

    const inspection_date = inspectionDate.substr(5,2) + "/" + inspectionDate.substr(8,2) + "/" + inspectionDate.substr(0,4)

    const options = {
      headers: {
        inspection_id: inspectionID.mc + "-" + inspectionID.iteration + "-" + inspectionID.type,
        //relative_url: "2022InspRpt/SU" + currentSU + "MF" + currentMF
        relative_url: sharePointPath.concat("/", "SU", currentSU, "MF", currentMF),
        inspection_date: inspection_date
      }
    }

    try {
      setShowSharePointFiles(false)
      setUploadResult([])
      for (let i = 0; i < files.length; i++) {
        const formData = new FormData()
        formData.append("files[]", files[i])
        try {
          //console.log("uploading : ", files[i].name)
          await trackPromise(axios.post("/api/upload", formData, options), "sharePointUpload")
          setDisplayAlert({ message: "Successfully uploaded " + files[i].name + " !!", position: 4, timeout: 3000, color: "alert__green" })
          setUploadResult(prev=>prev.concat({ file: files[i].name, status: "Complete", reason:"N/A"}))
        } catch (error) {

          // default error message to capture at least the time out
          let errMessage =  "File name may have some special character(s) or the upload time is over 30s !"      

          const errData = error.message
          
          if (errData !== undefined )  {
            if (error.message.includes("501")) 
               errMessage = "File name has already existed for this manufacturer !" 
            else if (error.message.includes("502")) 
               errMessage = "Your file type is not allowed !"         
          }                    
            
          errMessage = "File '" + files[i].name + "' : " + errMessage
          setDisplayAlert({ message: errMessage, position: 4, timeout: 5000, color: "alert__red" })
          setUploadResult(prev=>prev.concat({ file: files[i].name, status: "Failed", reason:errMessage }))
        }
      } 
      
      document.getElementById("sharePoint_file").value = null      
      //document.getElementById("sharePoint_refresh").click()
    } catch (err) {
      setDisplayAlert({ message: "Unexpected upload error !!", osition: 3, timeout: 5000, color: "alert__red" })     
    }    
  }

  // encapsulate the props function as only message is different in this page
  // const displayAlert = message => {
  //   setDisplayAlert({ message: message, position: 3, timeout: 3000, color: "alert__red" })
  // }

  return (
    <Fragment>
      <div className="uploadDoc">
           <form>
            <input type="file" multiple name="file" id="sharePoint_file" onChange={handleFile} />
            <button type="button" onClick={handleUpload}>
              Upload files
            </button>
          </form>
       </div>
    </Fragment>
  )
}

export default UploadForm