import { React, useEffect, useState } from "react"

const ItemTotal = props => {
  const { items, mcTable, addMC, currentSU, currentMF, editMode, setDisplayAlert, ship, setShip, poList, totalShip, totalInspect, totalSample, reset, handleDeleteShip, handleDeletePO, setPOList, aqlTable, handleHighlight,
  checkedState, setCheckedState, deleteShipMulti } = props

  const [mc, setMC] = useState("")
  const [summary, setSummary] = useState(true)
  const [shipSummary, setShipSummary] = useState([])    
 

  useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        //console.log("Enter key was pressed. Run your function.");
        event.preventDefault()
        document.getElementById("itemTable_MCBtn").click()
      }
    }

    if (document.getElementById("itemTable_MC")) document.getElementById("itemTable_MC").addEventListener("keydown", listener)

    return () => {
      if (document.getElementById("itemTable_MC")) document.getElementById("itemTable_MC").removeEventListener("keydown", listener)
    }

  }, [])

  /* Item Total */

  useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        //console.log("Enter key was pressed. Run your function.");
        event.preventDefault()
        document.getElementById("highLightItemBtn").click()
      }
    }
    var el = document.getElementById("itemTotal_searchString")

    if (el) {
      el.addEventListener("keydown", listener)
      return () => {
        el.removeEventListener("keydown", listener)
      }
    }
  }, [])

  const sortTable = (sort, order) => {
    handleHighlight("")
    const nShip = [...ship]
    /*table = document.getElementById("itemTable") */
    if (sort === 0 && order === "asc") {
      nShip.sort(function (a, b) {
        return a.po_no > b.po_no ? 1 : -1
      })
    } else if (sort === 0 && order === "desc") {
      nShip.sort(function (a, b) {
        return a.po_no < b.po_no ? 1 : -1
      })
    } else if (sort === 1 && order === "asc") {
      nShip.sort(function (a, b) {
        return a.item_no > b.item_no ? 1 : -1
      })
    } else if (sort === 1 && order === "desc") {
      nShip.sort(function (a, b) {
        return a.item_no < b.item_no ? 1 : -1
      })
    }
    setShip(nShip)
  }

  const sortSummaryTable = (sort, order) => {
    handleHighlight("")
    const nSum = [...shipSummary]
    /*table = document.getElementById("itemTable") */
    if (sort === 0 && order === "asc") {
      nSum.sort(function (a, b) {
        return a.item_no > b.item_no ? 1 : -1
      })
    } else if (sort === 0 && order === "desc") {
      nSum.sort(function (a, b) {
        return a.item_no < b.item_no ? 1 : -1
      })
    }
    setShipSummary(nSum)
  }

  //* add new MC

  const handleInputChange = event => {
    const value = event.target.value
    setMC(value)
  }

  const validateMC = () => {
    //event.preventDefault()
    const inMCTable = mcTable.find(x => x.mc_no === mc)
    const inItems = items.find(x => x.mc_no === mc)
    const duplicate = ship.find(x => x.mc_no === mc)
    const validMF = mcTable.find(x => x.mc_no === mc && x.su_no === currentSU && x.mf_no === currentMF)

    if (!inMCTable) {
      setDisplayAlert({ message: "This MC is not found in forthcoming or may not belong to this manufacturer!", position: 1, timeout: 4000, color: "alert__red" })
    } else if (!validMF) {
      displayAlert("This MC seems not for this manufacturer!")
    } else if (duplicate) {
      displayAlert("MC was already entered in the ship item list, please check ! ")
    } else if (inMCTable || !inItems) {
      addMC(mc)
    }
  }

  //encapsulate the props function as only message is different in this page
  const displayAlert = message => {
    setDisplayAlert({ message: message, position: 1, timeout: 1500, color: "alert__red" })
  }

  const handleShipQty = (po_no, item_no) => event => {
    let value = event.target.value

    if (value === "") {
      return
    }
    if (parseInt(value) > 30000) {
      value = 30000
      setDisplayAlert({ message: "Number of ship units is too large!", position: 1, timeout: 3000, color: "alert__red" })
    }

    let nShip = [...ship]
    const index = nShip.findIndex(r => r.po_no === po_no && r.item_no === item_no)
    const newEntry = nShip.find(r => r.po_no === po_no && r.item_no === item_no)
    nShip[index] = { ...newEntry, ship_qty: parseInt(value) }
    //console.log("handleShipQty", nShip)

    // recalculate sample
    const total_qty = nShip.reduce((prev, curr) => prev + parseInt(curr.ship_qty), 0) // total new ship qty
    const aql = aqlTable.filter(x => x.size_start <= total_qty && x.size_end >= total_qty)
    nShip = nShip.map(x => ({ ...x, sample_qty: Math.round((x.ship_qty / total_qty) * aql[0]["25sample"]), inspect_qty: Math.round((x.ship_qty / total_qty) * aql[0]["25sample"]) }))

    setShip(nShip)

    const tShip = nShip.filter(x => x.po_no === po_no).reduce((prev, curr) => prev + parseInt(curr.ship_qty), 0) // total new ship qty
    const nPO = [...poList]
    //console.log("nPO", nPO)
    const indexPO = nPO.findIndex(r => r.po_no === po_no)
    const newEntryPO = nPO.find(r => r.po_no === po_no)
    nPO[indexPO] = { ...newEntryPO, ship_qty: tShip }
    setPOList(nPO)
  }

  const handleInspQty = (po_no, item_no) => event => {
    let value = event.target.value
    const nShip = [...ship]
    const index = nShip.findIndex(r => r.po_no === po_no && r.item_no === item_no)
    const newEntry = nShip.find(r => r.po_no === po_no && r.item_no === item_no)
    nShip[index] = { ...newEntry, inspect_qty: parseInt(value) }
    setShip(nShip)
  }

  const toggleSummary = () => {
    // reconstruct PO based on ship rec (existing and newly added)

    let nSummary = []
    let cShip = [...ship]
    let allItems = cShip.map(x => x.item_no)
    let uniqueItems = allItems.filter((v, i, a) => a.indexOf(v) === i)

    uniqueItems.forEach(function (u) {
      const order_qty = cShip.filter(x => x.item_no === u).reduce((prev, curr) => prev + parseInt(curr.order_qty), 0)
      const ship_qty = cShip.filter(x => x.item_no === u).reduce((prev, curr) => prev + parseInt(curr.ship_qty), 0)
      const sample_qty = cShip.filter(x => x.item_no === u).reduce((prev, curr) => prev + parseInt(curr.sample_qty), 0)
      const inspect_qty = cShip.filter(x => x.item_no === u).reduce((prev, curr) => prev + parseInt(curr.inspect_qty), 0)
      nSummary.push({ item_no: u, order_qty, ship_qty, sample_qty, inspect_qty })
    })

    setShipSummary(nSummary)
    setSummary(prev => !prev)
  }


  const handleToggle = () => {
    let toggle = document.getElementsByName("selectToggle")
    if (toggle[0].checked) {  // control checked, it forces to check all rows
      handleSelectAll()
    }
    else {
      handleClearAll()
    }
  }  
  
  const handleSelectAll = () => {
    let allCheckboxes = document.getElementsByName("selectRow")
    for (var i = 0; i < allCheckboxes.length; i++) {
      allCheckboxes[i].checked = true
      checkedState[i] = true
    }
    setCheckedState(checkedState)
  }

  const handleClearAll = () => {
    let allCheckboxes = document.getElementsByName("selectRow")    
    for (var i = 0; i < allCheckboxes.length; i++) {
      allCheckboxes[i].checked = false
      checkedState[i] = false
    }
    setCheckedState(checkedState)
  }

  const handleOnChange = position => {    
    const updatedCheckedState = checkedState.map((item, index) => (index === position ? !item : item))
    setCheckedState(updatedCheckedState)
  }
  
  const deleteRows = () => {          
    
    let nShip = [...ship]    
    var elements = document.getElementsByClassName("clickrow");
    for (var i = 0; i < elements.length; i++) {
      if (checkedState[i]) { 
          let rec = nShip[i]
          rec = {...rec, del : true}  
          nShip[i] = rec
      }
    }     
    nShip = nShip.filter(x=>!x.del)
    deleteShipMulti(nShip)               
    handleClearAll()

  }

  return (
    // present the table

    <div className="itemTable_2col">
      <div>
        <div className="itemTable_Section">
          <div>Add MC:</div>
          &nbsp;
          <div>{editMode ? <input type="text" id="itemTable_MC" name="itemTable_mc" justify="left" placeholder="Additional MC here" onChange={handleInputChange} /> : <input type="text" id="itemTable_MC" name="itemTable_MC" justify="left" placeholder="Include additional MC here" readOnly onChange={handleInputChange} />}</div>
          &nbsp;&nbsp;
          <div>
            {editMode ? (
              <i
                id="itemTable_MCBtn"
                className="fa fa-plus clickable"
                onClick={() => {
                  validateMC(mc)
                }}
              ></i>
            ) : null}
          </div>
        </div>
        <div className="itemTable_Header">
          <div>PO</div>
          <div>DIV</div>
          <div>Order Qty</div>
          <div>Ship Qty</div>
          <div>%Ship</div>
          <div>Delete</div>
          <div></div>
        </div>

        <div id="itemTableScroller" className="itemTable_GridContainer">
          {!poList
            ? null
            : poList.map((x, index) => (
                <div key={index} className="itemTable_Detail">
                  <div>{x.po_no}</div>
                  <div>{x.customer}</div>
                  <div>{x.order_qty}</div>
                  <div>{x.ship_qty}</div>
                  <div>{Math.round((x.ship_qty / x.order_qty) * 100)}%</div>
                  <div>
                    {/* <div>{editMode ? <button onClick={e => deletePO(x.po_no)}>x</button> : null}</div> */}
                    {editMode ? (
                      <i
                        className="fa fa-times clickable"
                        onClick={() => {
                          handleDeletePO(x.mc_no, x.po_no)
                        }}
                      ></i>
                    ) : null}
                  </div>
                </div>
              ))}
        </div>
      </div>
      <div></div>
      <div>
        <div className="itemTotal_Section">    
        
          {editMode ? (
            <div>  
              {summary ? "Summary" : "Edit"}
              {editMode ? (
                <i
                  className="fa fa-refresh itemTotal_undo_color clickable "
                  onClick={() => {
                    toggleSummary()
                    handleHighlight("")
                  }}
                ></i>
              ) : null}
            </div>
          ) : null}

          {editMode ? (
            <div>
              Multi Del&nbsp;
              <i
                className="fa fa-times itemTotal_refresh_color clickable"
                onClick={() => {
                  deleteRows()
                }}
              ></i>
            </div>
          ) : null}


          {editMode ? (
            <div>
              Reset Inspect &nbsp;
              <i
                className="fa fa-refresh itemTotal_refresh_color clickable"
                onClick={() => {
                  reset()
                }}
              ></i>
            </div>
          ) : null}
          
          <div>
            Highlight 
            &nbsp;                     
            <input type="text" name="itemTotal_searchString" id="itemTotal_searchString" justify="left" onInput={e => (e.target.value = ("" + e.target.value).toUpperCase())} placeholder="Enter item#" />
            &nbsp;                    
            <i
              className="fa fa-search clickable"
              id="highLightItemBtn"
              onClick={() => {
                handleHighlight(document.getElementById("itemTotal_searchString").value)
              }}
            ></i>
          </div>
        </div>

        {summary ? (
          <div>
            <div className="itemTotal_Header">
              <div>
                {editMode ? (
                <input type="checkbox" name="selectToggle" onChange={() => handleToggle()}  /> 
                ):null}        
              </div>
              <div>
                PO
                <i
                  className="fa fa-chevron-circle-up clickable"
                  onClick={() => {
                    sortTable(0, "asc")
                  }}
                ></i>
                &nbsp;
                <i
                  className="fa fa-chevron-circle-down clickable"
                  onClick={() => {
                    sortTable(0, "desc")
                  }}
                ></i>
              </div>
              <div>
                By Item
                <i
                  className="fa fa-chevron-circle-up clickable"
                  onClick={() => {
                    sortTable(1, "asc")
                  }}
                ></i>
                &nbsp;
                <i
                  className="fa fa-chevron-circle-down clickable"
                  onClick={() => {
                    sortTable(1, "desc")
                  }}
                ></i>
              </div>
              <div>Ship</div>
              <div>Sample</div>
              <div>Inspect</div>
              <div>Delete</div>
            </div>

            <div id="scroller" className="itemTotal_GridContainer">
              {!ship
                ? null
                : ship.map((x, index) => (
                      <div key={index} className="itemTotal_Detail">
                         <div>
                            {editMode ? (                                               
                              <input type="checkbox" name="selectRow" onChange={() => handleOnChange(index)} />          
                            ):null}        
                         </div>                 
                      <div>{x.po_no}</div>
                      <div>{x.item_no}</div>
                      <div>
                        <input type="number" id="itemTotal_shipQty" name="itemTotal_shipQty" value={x.ship_qty || 1} min="1" max="30000" onChange={handleShipQty(x.po_no, x.item_no)} readOnly={editMode ? "" : "readOnly"} />
                      </div>
                      <div>{x.sample_qty}</div>

                      <div>
                        <input type="number" id="itemTotal_InspectQty" name="itemTotal_InspectQty" value={x.inspect_qty || 1} min="1" max="30000" onChange={handleInspQty(x.po_no, x.item_no)} readOnly={editMode ? "" : "readOnly"} />
                      </div>

                      <div>
                        {editMode ? (
                          <i
                            className="fa fa-times clickable clickrow"
                            onClick={() => {
                              handleDeleteShip(x.mc_no, x.po_no, x.item_no)
                            }}
                          ></i>
                        ) : null}
                      </div>
                    </div>
                  ))}
            </div>
          </div>
        ) : (
          <div>
            <div className="itemTotal_Header2">
              <div></div>
              <div>
                By Item
                <i
                  className="fa fa-chevron-circle-up clickable"
                  onClick={() => {
                    sortSummaryTable(0, "asc")
                  }}
                ></i>
                &nbsp;
                <i
                  className="fa fa-chevron-circle-down clickable"
                  onClick={() => {
                    sortSummaryTable(0, "desc")
                  }}
                ></i>
              </div>
              <div>Ship</div>
              <div>Sample</div>
              <div>Inspect</div>
              <div></div>
              <div></div>
            </div>

            <div id="scroller" className="itemTotal_GridContainer">
              {!shipSummary
                ? null
                : shipSummary.map((x, index) => (
                    <div key={index} className="itemTotal_Detail">
                      <div></div>
                      <div>{x.item_no}</div>
                      <div>{x.ship_qty}</div>
                      <div>{x.sample_qty}</div>
                      <div>{x.inspect_qty}</div>
                      <div></div>
                    </div>
                  ))}
            </div>
          </div>
        )}

        {summary ? (
          <div>
            <div className="itemTotal_Header">
              <div></div>
              <div></div>
              <div> Total :</div>
              <div>{totalShip}</div>
              <div>{totalSample}</div>
              <div className={totalSample !== totalInspect ? "highlightSection" : null}>{totalInspect}</div>              
              <div></div>
            </div>
          </div>
        ) : (
          <div>
            <div className="itemTotal_Header2">
              <div></div>
              <div> Total :</div>
              <div>{totalShip}</div>
              <div>{totalSample}</div>
              <div className={totalSample !== totalInspect ? "highlightSection" : null}>{totalInspect}</div>
              <div></div>
              <div></div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}

export default ItemTotal
