import React, { Fragment } from "react"
 
const UploadResult = props => {
  const { uploadResult} = props
 
 
  return (
    // present the table
    <Fragment>

      <div className="uploadResult_section">

          {uploadResult.length > 0
            ?  (

          <div className="uploadResult_grid_header">
            <div>Uploaded File</div>
            <div>Status</div>
            <div>Hint</div>            
          </div>
            ) : null }

          {uploadResult.length > 0
            //make a copy and how the copied list in reverse without affecting the original order
            ? uploadResult.slice(0).reverse().map((x, index ) => (
                <div
                  className="uploadResult_grid_detail"
                  key={index}             
                > 
                  <div>{x.file}</div>
                  <div className={x.status === "Failed" ? "uploadResult_red": null}>{x.status}</div>                  
                  <div>{x.reason}</div>
                </div>
              ))
            : null}
      </div>
   
    </Fragment>
  )
}

export default UploadResult
