import React, { useState, useEffect } from "react"
import DatePicker from "react-datepicker"

const Misc = props => {
  const { setInspectionDate, inspectionDate, setMisc, misc, editMode, mqaMembers, packTypes, userProfile, productCategories } = props
 
  const [isSQA, setISsqa] = useState("")  

  // Following is needed to make sure Inspection Date is not empty when it is a default and date is not selected in the calendar widget. 
  // Otherwise, inspection date will become empty captured in the Mongo DB.
  useEffect(() => {


    if (inspectionDate === "") {      
      let d = new Date()    
      const userTimezoneOffset = d.getTimezoneOffset() * 60000;
      d = new Date(d.getTime() - userTimezoneOffset);           
      setInspectionDate(d.toISOString().substring(0,10))          
    }
  }, [inspectionDate, setInspectionDate])
  // 
  
  useEffect(() => {
    
    let newMisc = { ...misc }

    if (newMisc.qa_type === undefined) {
                        
        if (userProfile !==undefined && userProfile.email !==undefined  ){

          if (userProfile.email.includes("@macys.com")) {
            newMisc =  { ...newMisc, qa_type : "MQA" }            
            setMisc(newMisc)                   
          }
          else {            
            newMisc = { ...newMisc,  qa_type : "SQA", sqa: userProfile.email,  governance_inspection: undefined, sqa_iteration: undefined, cap: undefined }       
            setISsqa(true)        
            setMisc(newMisc)                   
          } 

        }         
            
    }

    if (misc.qa_type === "SQA") {
      setISsqa(true)
    }
    
  }, [misc, userProfile, setMisc])

  // This is needed to mitigate the dates between browser (local time) and server (Mongo DB)
  // When date (date.getTime) is selected, the date is based on UTC 00:00.  That could be the next day. 
  // Therefore, the offset has to be calcualted and be used to minus the date  
  const dateToString = date => {      
    const userTimezoneOffset = date.getTimezoneOffset() * 60000;
    const d = new Date(date.getTime() - userTimezoneOffset);
    //console.log('d', d.toISOString().substring(0,10))
    setInspectionDate(d.toISOString().substring(0,10))    
  }
  
  // This is needed to mitigate the dates between browser (local time) and server (Mongo DB)
  // When date (date string) is captured, it is based on UTC 00:00. So the local time could become the previous day. 
  // Therefore, the offset has to be calcualted and be used to add to the date string   

  const stringToDate = dateString => {    
    let d = dateString       
    if (d === "") {      
      d = new Date()    
      const userTimezoneOffset = d.getTimezoneOffset() * 60000;
      d = new Date(d.getTime() - userTimezoneOffset);           
      //setInspectionDate(d.toISOString().substring(0,10))          
    }
    else {
      d = new Date(dateString)         
      const userTimezoneOffset = d.getTimezoneOffset() * 60000;
      d = new Date(d.getTime() + userTimezoneOffset);               
    }
    return d        
  }
  

  // Misc Fields (Text) Capture
  const handleChange = (element, yes_or_no) => event => {
    let value = event.target.value

    if (misc) {
      let newMisc = { ...misc }
      switch (element) {
        case 2: //b
          newMisc = { ...newMisc, product_type: value }
          break
        case 3: //c
          newMisc = { ...newMisc, lab_test_report: value }
          break
        case 4: //d
          newMisc = { ...newMisc, lab_child_safety_report: value }
          break
        case 5: //e
          newMisc = { ...newMisc, fibre_content: value }
          break
        case 6: //f
          newMisc = { ...newMisc, comments: value }
          break
        case 7: //g
          newMisc = { ...newMisc, governance_inspection: yes_or_no }
          break
        case 8: //h
          newMisc = { ...newMisc, props65_report: value }
          break
        case 9: //i
          newMisc = { ...newMisc, ship_mode: value }
          break
        case 10: //j
          newMisc = { ...newMisc, pack_pct: value }
          break
        case 11: //k
          newMisc = { ...newMisc, ship_window: value }
          break
        case 12: //l
          newMisc = { ...newMisc, carton_list: value }
          break
        case 13: //m
          newMisc = { ...newMisc, carton_total: value }
          break
        case 14: //n
          newMisc = { ...newMisc, pack_type: value }
          break
        case 15: //O
          newMisc = { ...newMisc, qa_type: value }
          if (value === "SQA") {
              newMisc = { ...newMisc, sqa: userProfile.email,  governance_inspection: undefined, sqa_iteration: undefined, cap: undefined }       
              setISsqa(true)        
          }
          if (value === "MQA" && newMisc.mqa === undefined)  newMisc = { ...newMisc, mqa: userProfile.email }                
          if (value === "MQA")  setISsqa(false)        
          break
        case 16: //p
          newMisc = { ...newMisc, mqa: value }
          break
        case 17: //p
          newMisc = { ...newMisc, sqa: value }
          break
        case 18: //q
          newMisc = { ...newMisc, correlation_inspection: yes_or_no }
          break
        case 19: //r
          newMisc = { ...newMisc, product_category: value }
          break
        case 20: //s
          newMisc = { ...newMisc, label: value }
          break
        case 21: //t
          newMisc = { ...newMisc, sqa_iteration: value }
          break
        case 22: //u
          newMisc = { ...newMisc, cap:  yes_or_no  }
          break


        default:
          console.log("No element")
      }
      setMisc(newMisc)
    }
  }

  return (
    <div className="misc">
      <div className="misc_a">
        <div className="misc_title misc_grid_a">
          <div>Inspection Date (A)</div>
          <DatePicker className="datePicker" dateFormat="MM/dd/yy" selected={stringToDate(inspectionDate)} onChange={date => dateToString(date)} readOnly={editMode ? "" : "readOnly"} />
        </div>
      </div>

      <div className={ "misc_g misc_grid_g " +  (isSQA? "misc_protect" :undefined ) }>
        <div className="misc_title">Governance Inspection (G)</div>
        <div>
          <input type="radio" id="misc_governance_inspection_yes" name="misc_governance_inspection" value={misc.governance_inspection || ""} checked={misc.governance_inspection === "Yes"} onChange={handleChange(7, "Yes")} disabled={editMode && !isSQA ? "" : "disabled"} />
        </div>
        <div>Yes</div>
        <div></div>
        <div>
          <input type="radio" id="misc_governance_inspection_no" name="misc_governance_inspection" value={misc.governance_inspection || ""} checked={misc.governance_inspection === "No"} onChange={handleChange(7, "No")} disabled={editMode && !isSQA ? "" : "disabled"} />
        </div>
        <div>No</div>
      </div>     

      <div className={ "misc_t misc_grid_t " +  (isSQA? "misc_protect" :undefined ) }>

        <div className="misc_title">Governance SQA Iteration (T)</div>
        <div>
          <input type="number" name="misc_sqa_iteration" id="misc_sqa_iteration" value={misc.sqa_iteration || ""} placeholder="" min="1" max="99" onChange={handleChange(21)}   disabled={editMode && !isSQA  ? "" : "disabled"} />
        </div>        
      </div>

      <div className="misc_q misc_grid_q">
        <div className="misc_title">Correlation Inspection (Q)</div>
        <div>
          <input type="radio" id="misc_correlation_inspection" name="misc_correlation_inspection" value={misc.correlation_inspection || ""} checked={misc.correlation_inspection === "Yes"} onChange={handleChange(18, "Yes")}  disabled={editMode ? "" : "disabled"} />
        </div>
        <div>Yes</div>
        <div></div>
        <div>
          <input type="radio" id="misc_correlation_inspection" name="misc_correlation_inspection" value={misc.correlation_inspection || ""} checked={misc.correlation_inspection === "No"} onChange={handleChange(18, "No")}  disabled={editMode ? "" : "disabled"} />
        </div>
        <div>No</div>
      </div>


      <div className="misc_j misc_grid_j">
        <div className="misc_title">Pack % During Inspection (J)</div>
        <input type="text" id="misc_packPct" name="misc_packPct" value={misc.pack_pct || ""} justify="left" placeholder="%" onChange={handleChange(10)} readOnly={editMode ? "" : "readOnly"} />
      </div>

      <div className="misc_k misc_grid_k">
        <div className="misc_title">Ship Window (K)</div>
        <input type="text" id="misc_shipWindow" name="misc_shipWindow" value={misc.ship_window || ""} justify="left" placeholder="Ship Window" onChange={handleChange(11)} readOnly={editMode ? "" : "readOnly"} />
      </div>

      <div className="misc_b misc_grid_b">
        <div className="misc_title">Product Type (B)</div>
        <input type="text" id="misc_productType" name="misc_productType" value={misc.product_type || ""} justify="left" placeholder="Product Type" onChange={handleChange(2)} readOnly={editMode ? "" : "readOnly"} style={{ textTransform: "uppercase" }} />
      </div>

      <div className="misc_s misc_grid_s">
        <div className="misc_title">Brand (S)</div>
        <input type="text" id="misc_label" name="misc_label" value={misc.label || ""} justify="left" onChange={handleChange(20)} readOnly={editMode ? "" : "readOnly"} style={{ textTransform: "uppercase" }} />
      </div>

      <div className="misc_n misc_grid_n">
        <div className="misc_title">Pack Type (N)</div>

        <select id="misc_pack_type" name="misc_pack_type" value={misc.pack_type || "Select"} justify="left" placeholder=" " onChange={handleChange(14)}>
          <option value="Select">Select</option>
          {packTypes &&
            packTypes.map(x => (
              <option key={x.type} value={x.type} disabled={editMode ? "" : "disabled"}>
                {x.type}
              </option>
            ))}
        </select>
      </div>  

      <div className="misc_o misc_grid_o">
        <div className="misc_title">QA Type (O)</div>

        <select id="misc_qaType" name="misc_qaType" value={misc.qa_type || "Select"} justify="left" placeholder="QA Type" onChange={handleChange(15)}>
          <option value="Select" disabled="disabled">
            Select
          </option>
          {/* <option value="MQA" disabled={editMode ? "" : "disabled"}>
            MQA
          </option>
          <option value="SQA" disabled={editMode ? "" : "disabled"}>
            SQA
          </option> */}
          <option value="MQA" disabled={"disabled"}>
            MQA
          </option>
          <option value="SQA" disabled={"disabled"}>
            SQA
          </option>
        </select>
      </div>


      <div className="misc_c misc_grid_c">
        <div className="misc_title">Lab Test Report (C)</div>

        <select id="misc_labTest" name="misc_labTest" value={misc.lab_test_report || "Select"} justify="left" placeholder="Test result" onChange={handleChange(3)}>
          <option value="Select" disabled="disabled">
            Select
          </option>
          <option value="N/A" disabled={editMode ? "" : "disabled"}>
            N/A
          </option>
          <option value="Pending" disabled={editMode ? "" : "disabled"}>
            Pending
          </option>
          <option value="Pass" disabled={editMode ? "" : "disabled"}>
            Pass
          </option>
          <option value="Fail" disabled={editMode ? "" : "disabled"}>
            Fail
          </option>
        </select>
      </div>

      <div className="misc_d misc_grid_d">
        <div className="misc_title">Child Safety Report (D)</div>

        <select id="misc_childSafetyReport" name="misc_childSafetyReport" value={misc.lab_child_safety_report || "Select"} justify="left" placeholder=" " onChange={handleChange(4)}>
          <option value="Select" disabled="disabled">
            Select
          </option>
          <option value="N/A" disabled={editMode ? "" : "disabled"}>
            N/A
          </option>
          <option value="Pending" disabled={editMode ? "" : "disabled"}>
            Pending
          </option>
          <option value="Accept" disabled={editMode ? "" : "disabled"}>
            Accept
          </option>
          <option value="Reject" disabled={editMode ? "" : "disabled"}>
            Reject
          </option>
        </select>
      </div>

      <div className="misc_h misc_grid_h">
        <div className="misc_title">Props 65 Report (H)</div>
        <select id="misc_props65Report" name="misc_props65Report" value={misc.props65_report || "Select"} justify="left" placeholder=" " onChange={handleChange(8)}>
          <option value="Select" disabled="disabled">
            Select
          </option>
          <option value="N/A" disabled={editMode ? "" : "disabled"}>
            N/A
          </option>
          <option value="Pending" disabled={editMode ? "" : "disabled"}>
            Pending
          </option>
          <option value="Accept" disabled={editMode ? "" : "disabled"}>
            Accept
          </option>
          <option value="Reject" disabled={editMode ? "" : "disabled"}>
            Reject
          </option>
        </select>
      </div>

      <div className="misc_i misc_grid_i">
        <div className="misc_title">Ship Mode (I)</div>

        <select id="misc_shipMode" name="misc_shipMode" value={misc.ship_mode || "Select"} justify="left" placeholder=" " onChange={handleChange(9)}>
          <option value="Select" disabled="disabled">
            Select
          </option>
          <option value="Ocean" disabled={editMode ? "" : "disabled"}>
            Ocean
          </option>
          <option value="Air" disabled={editMode ? "" : "disabled"}>
            Air
          </option>
        </select>
      </div>

 

      <div className="misc_e misc_grid_e">
        <div className="misc_title">Fibre Content (E)</div>
        <input type="text" id="misc_fibreContent" name="misc_fibreContent" value={misc.fibre_content || ""} justify="left" placeholder="Fibre Content" onChange={handleChange(5)} readOnly={editMode ? "" : "readOnly"} />
      </div>

      <div className="misc_f misc_grid_f">
        <div className="misc_title">Comments (F)</div>
        <textarea rows="5" cols="50" id="misc_comments" name="misc_comments" value={misc.comments || ""} justify="left" placeholder="Comments" onChange={handleChange(6)} readOnly={editMode ? "" : "readOnly"} />
      </div>

      <div className="misc_l misc_grid_l">
        <div className="misc_title">Carton List (L)</div>
        <textarea rows="3" cols="50" id="misc_cartonList" name="misc_cartonList" value={misc.carton_list || ""} justify="left" placeholder="Carton List" onChange={handleChange(12)} readOnly={editMode ? "" : "readOnly"} />
      </div>

      <div className="misc_m misc_grid_m">
        <div className="misc_title">Carton Total (M)</div>
        <input type="text" id="misc_cartonTotal" name="misc_cartonTotal" value={misc.carton_total || ""} justify="left" placeholder="Carton Total" onChange={handleChange(13)} readOnly={editMode ? "" : "readOnly"} />
      </div>

      <div className="misc_i misc_grid_i">
        <div className="misc_title">Ship Mode (I)</div>
        <select id="misc_shipMode" name="misc_shipMode" value={misc.ship_mode || "Select"} justify="left" placeholder=" " onChange={handleChange(9)}>
          <option value="Select" disabled="disabled">
            Select
          </option>
          <option value="Ocean" disabled={editMode ? "" : "disabled"}>
            Ocean
          </option>
          <option value="Air" disabled={editMode ? "" : "disabled"}>
            Air
          </option>
        </select>
      </div>

      <div className="misc_p misc_grid_p">
        <div className="misc_title">{misc.qa_type === "MQA" || misc.qa_type === undefined ? <span>MQA (P)</span> : <span>SQA (P)</span>}</div>
        {misc.qa_type === "MQA" || misc.qa_type === undefined ? (
          <select id="misc_mqa" name="misc_mqa" value={misc.mqa || "Select"} justify="left" placeholder=" " onChange={handleChange(16)}>
            <option value="Select">Select</option>
            {mqaMembers &&
              mqaMembers.map(x => (
                <option key={x.mqa} value={x.mqa} disabled={editMode ? "" : "disabled"}>
                  {x.mqa}
                </option>
              ))}
          </select>
        ) : (
          <input type="text" id="misc_sqa" name="misc_sqa" value={misc.sqa || ""} justify="left" placeholder="Inspector" onChange={handleChange(17)} readOnly="readOnly" />
        )}
      </div>
 


      <div className="misc_r misc_grid_r">
        <div className="misc_title">Product Category (R)</div>

        <select id="misc_product_category" name="misc_product_category" value={misc.product_category || "Select"} justify="left" placeholder=" " onChange={handleChange(19)}>
          <option value="Select">Select</option>
          {productCategories &&
            productCategories.map(x => (
              <option key={x.category} value={x.category} disabled={editMode ? "" : "disabled"}>
                {x.category}
              </option>
            ))}
        </select>
      </div>      




      <div className={"misc_u misc_grid_u " +  (isSQA? "misc_protect" :undefined ) }>          
        <div className="misc_title">Corrective Action Plan Required (U)</div>
        <div>
          <input type="radio" id="misc_cap" name="misc_cap" value={misc.cap || ""} checked={misc.cap === "Yes"} onChange={handleChange(22, "Yes")}  disabled={editMode && !isSQA  ? "" : "disabled"} />
        </div>
        <div>Yes</div>
        <div></div>
        <div>
          <input type="radio" id="misc_cap" name="misc_cap" value={misc.cap || ""} checked={misc.cap === "No"} onChange={handleChange(22, "No")}  disabled={editMode && !isSQA  ? "" : "disabled"} />
        </div>
        <div>No</div>
      </div>




 

    </div>
  )
}
export default Misc
